

.btn{
    /*
link is a state of the button selector
this is a pseudoclass to style things under a certain condition
the other state would be the visited state
we dont want weird hyperlink 
*/
    &:link, 
    &:visited {
        text-transform: uppercase;
        text-decoration: none; 
        padding: 15px 40px;
        display: inline-block;
        border-radius: 100px;
        transition: all .2s;
        position: relative;
    }
    
    &:hover{
        transform: translateY(-3px);
        box-shadow: 0 10px 20px rgba($color-black,0.2);
        &::after{
            transform: scaleX(1.4) scaleY(1.6);
            opacity: 0;
        }
    }
    
    &:active{
        transform: translateY(-1px);
        box-shadow: 0 5px 10px rgba($color-black,0.2);
    }
    
    &-white{
        background-color: $color-white;
        color: $color-grey-dark;
        &::after{
            background-color: $color-white;
        }
    }
    
    &::after{
        content: "";
        display: inline-block;
        height: 100%;
        width: 100%;
        border-radius: 100px;
        position: absolute;
        top: 0;
        left: 0;
        z-index:-1;
        transition: all .4s;
    }
    
    &-animated{
        animation: moveInBottom .5s ease-out .75s;
        animation-fill-mode: backwards;
    }

}

.btn-text {
    &:link,
    &:visited{
        color: $color-tertiary-dark;
        display:inline-block;
        text-decoration: none;
        border-bottom: 1px solid $color-tertiary-dark;
        padding:3px;
        font-size: $default-font-size;
        transition: all 0.2s;
    }

    &:hover{
        color: $color-white;
        background-color: $color-tertiary-light;
        box-shadow: 0 1rem 2rem rgba($color-black,0.15);
        transform: translateY(-2px);
    }

    &:active{
        box-shadow: 0 .5rem 1rem rgba($color-black,0.15);
        transform: translatey(0);
    }
}