
@keyframes moveInLeft {
    0% {
            opacity: 0;
            transform: translateX(-100px);
    }

    80%{
            transform: translateX(10px);
    }

    100%{
            opacity: 1;
            transform: translate(0);
    }
    /*
    0% initial state
    100% final state
   animation occurs at a % of the way 
   through its completion
    Optimization of animation in browsers allows 
    for coding of opacity and transformation.
    */
}

@keyframes moveInRight {
    0% {
            opacity: 0;
            transform: translateX(100px);
    }

    80%{
            transform: translateX(-10px);
    }

    100%{
            opacity: 1;
            transform: translate(0);
    }
    /*
    0% initial state
    100% final state
   animation occurs at a % of the way 
   through its completion
    Optimization of animation in browsers allows 
    for coding of opacity and transformation.
    */
}

@keyframes moveInBottom {
    0% {
            opacity: 0;
            transform: translateY(30px);
    }

    
    100%{
            opacity: 1;
            transform: translate(0);
