.heading-primary{
    color: $color-white;
    text-transform: uppercase;

    backface-visibility: hidden;
    margin-bottom: 60px;
    /*
    fixes shaking in element animations
    */

    &-main{
        display: block;
        font-size: 60px;
        font-weight: 400;
        letter-spacing: 35px;    
        
        /*
        Animation propertyies that are applied to the animation
        header.
        animation-delay: 3s;
        animation-iteration-count: 3;
        animation-timing-function: ;
        ease-out start fast end slow at end
        ease-in start slow end fast at end
        */
    
        animation-name: moveInLeft;
        animation-duration: 1s;
        animation-timing-function: ease-out;
           
    }
    &-sub{
        display: block;
        font-size: 20px;
        font-weight: 700;
        letter-spacing: 17.4px;
        animation: moveInRight 1s ease-out;
        }
}

body {
    font-family: "Lato", sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.7;
    color: $color-grey-dark;
    padding: 30px;
    
}
.heading-secondary{
    font-size: 3.5rem;
    text-transform: uppercase;
    font-weight: 700;
    display: inline-block;  
    background-image: linear-gradient(to right, $color-tertiary-light, $color-tertiary-dark);
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
    letter-spacing: 2px;
    transition: all .2s;

    &:hover{
        transform: skewY(2deg) skewX(15deg) scale(1.1);
        text-shadow:.5rem 1rem 2rem rgba($color-black,.2)
    }
}

.heading-tertiary{
    font-size: $default-font-size;
    font-weight: 700;
    text-transform: uppercase;
}

.paragraph{
    font-size: $default-font-size;
    &:not(:last-child){
        margin-bottom: 3rem;
    }
}